import AudioPlayer from "./AudioPlayer";



function AudioList(props) {

  //console.log("test - data", props.data);

  return (
    props.data.map(item =>{
      const {title, url:{url}} = item;
      return <li>
        <h3>{title}</h3>
       <AudioPlayer url={url} />
      </li>
    })
  )
}

export default AudioList